<template>
  <SimpleModalDlg :modalId="modalId" :title="` -  ${cno} - ${channelName}`" content="" defaultButtonText="" button2Text=""
    :button2Action="button2Action" :dialogWidth="1000">
    <template v-slot:bodyPost>
      <div class="container-fluid">
        <!-- <div class="row"> -->
        <div class="row">
          <div class="col-4">
            <div class="w-100 overflow-auto border-top border-bottom" style="height: 680px;">
              <div class="list-group" id="list-tab" role="tablist">
                <a 
                  v-for="item, i in Object.keys(channelAlgInfo)" :key="'alg' + modalId + i"
                  :class="'list-group-item list-group-item-action ' + ((i===0) ? 'active' : '')"
                  :id="`${modalId}-list-${item}`"
                  data-toggle="list"
                  :data-x="item"
                  role="tab"
                >
                  <div class="icon-name">
                    <div class="mr-2" style="max-height: 100%; width: 60px;">
                      <img :src="channelAlgInfo[item].icon" alt="">
                    </div>
                    <div style="width: 90px;">
                      <p class="mb-1"><b>{{channelAlgInfo[item].name}}</b></p>
                      <p v-if="channelAlgInfo[item].channelAlgorthm" class="my-0">
                        <svg class="icon svg-icon" aria-hidden="true">
                          <use xlink:href="#icon-dagou"></use>
                        </svg>
                        
                      </p>
                      <p v-else class="my-0">
                        <svg class="icon svg-icon" aria-hidden="true">
                          <use xlink:href="#icon-closel"></use>
                        </svg>
                        
                      </p>
                    </div>
                  </div>
                </a>
                <!-- <a class="list-group-item list-group-item-action" id="list-profile-list" data-toggle="list" href="#list-profile" role="tab" aria-controls="profile">Profile</a>
                <a class="list-group-item list-group-item-action" id="list-messages-list" data-toggle="list" href="#list-messages" role="tab" aria-controls="messages">Messages</a>
                <a class="list-group-item list-group-item-action" id="list-settings-list" data-toggle="list" href="#list-settings" role="tab" aria-controls="settings">Settings</a> -->
              </div>
            </div>
          </div>
          <div class="col-8 pl-0">
            <div class="row w-100 ml-1 py-2 border-bottom">
              <!-- <p v-if="channelAlgInfo[currAlgName].channelAlgorthm" class="my-0">
                <button class="btn btn-primary"></button>
              </p>
              <p v-else class="my-0">
                <button class="btn btn-primary"></button>
              </p> -->
              <label class="mt-1 field-label"></label>
              <span class="mt-1 mr-4">{{channelAlgInfo[currAlgName].name}}</span>
              <label class="mt-1 field-label" for="id-channel-alg-config"></label>
              <input class="ml-1" type="radio" id="id-channel-alg-config" name="channelAlgConfig">
              <label class="ml-2 mt-1 field-label" for="id-channel-alg-unconfig"></label>
              <input class="ml-2" type="radio" id="id-channel-alg-unconfig" name="channelAlgConfig">
            </div>
            <div class="row mt-2 pb-2 border-bottom">
              <span class="field-label pl-3"></span>
              <div class="container-fluid">
                <div class="row">
                  <div class="col-4 text-right">
                    <span class="">()</span>
                  </div>
                  <div class="col">
                    <input class="ml-2" type="number">
                  </div>
                </div>
                <div class="row mt-1">
                  <div class="col-4 text-right">
                    <span class="">()</span>
                  </div>
                  <div class="col">
                    <input class="ml-2" type="number">
                  </div>
                </div>
                <div class="row mt-1">
                  <div class="col-4 text-right">
                    <span class="">(0-1)</span>
                  </div>
                  <div class="col">
                    <input class="ml-2" type="number">
                  </div>
                </div>
                <div class="row mt-1">
                  <div class="col-4 text-right">
                    <span class="">(1-7)</span>
                  </div>
                  <div class="col">
                    <input class="ml-2" type="number" style="width: 5em;">
                    ~
                    <input class="ml-2" type="number" style="width: 5em;">
                  </div>
                </div>
                <div class="row mt-1">
                  <div class="col-4 text-right">
                    <span class="">(0-24)</span>
                  </div>
                  <div class="col">
                    <input class="ml-2" type="number" style="width: 5em;">
                    ~
                    <input class="ml-2" type="number" style="width: 5em;">
                  </div>
                </div>
              </div>
            </div>
            <div class="row mt-2 border-bottom">
              <div class="container-fluid">
                <div class="row">
                  <span class="field-label py-2 pl-3">(ROI)</span>
                  <label class="ml-4 pt-1 pl-3 mr-2 my-1" for="id-roi-whole-region"></label>
                  <input class="my-1" id="id-roi-whole-region" type="checkbox">
                </div>
                <div class="row ml-0 mt-2">
                  <div class="border w-100" style="height: 300px;">
                    <img src="../../assets/img/test-gongdi.jpeg" alt="" style="max-width: 100%; max-height: 100%">
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </template>
  </SimpleModalDlg>
</template>

<script>
import utils from '@/utils/utils'
import SimpleModalDlg from '@/components/utils/SimpleModalDlg'
import apis from '@/apis/apis'
import algorithmUtils from './algorithm-utils'
import $ from 'jquery'

const defaultAlgParams = {
  analyze_interval: 1000,
  alert_interval: 30,
  alert_threshold: 0.5,
  alert_week_start: 1,
  alert_week_end: 7,
  alert_day_start: 0,
  alert_day_end: 23
}

const defautRoi = {
  left: 0,
  top: 0,
  width: 0,
  height: 0
}

export default {
  name: 'ChannelAlgConfigDlg',
  components: {
    SimpleModalDlg,
  },
  data () {
    return {
      channelAlgInfo: utils.deepCopy(algorithmUtils.algNameIcons),
      currAlgName: Object.keys(algorithmUtils.algNameIcons)[0],
      currAlgParams: {...defaultAlgParams},
      currRoi: {...defautRoi}
    }
  },
  props: {
    modalId: {
      type: String,
      required: true
    },
    channelId: {
      type: Number,
      required: true
    },
    cno: {
      type: Number,
      required: true
    },
    channelName: {
      type: String,
      required: true
    }
  },
  computed: {
  },
  methods: {
    button2Action () {
      let endpoint = 'channel/channels/'

      let data = {}

      let request = apis.apiAjaxPost
      if (this.channelId) {
        // channelId
        endpoint = `${endpoint}/${this.channelId}/`
        request = apis.apiAjaxPut
      }

      request(endpoint, data,
        (res) => {
          if (res.code === 0) {
            alert('')
            this.$emit('channel-alg-saved')
          } else {
            alert(`(${res.code}): ${res.message}`)
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    },
    loadChannelAlgorithms () {
      let endpoint = `channel/channelalgorithms2/?channel__id=${this.channelId}`

      apis.apiAjaxGet(endpoint, {},
        (res) => {
          if (res.code === 0) {
            // 
            for (let k in this.channelAlgInfo) {
              this.channelAlgInfo[k].channelAlgorthm = null
            }
            // 
            res.data.forEach((v) => {
              // Update channelAlgInfo
              if (v.algorithm.name in this.channelAlgInfo) {
                alert(`: ${v.algorithm.name}`)
              } else {
                this.channelAlgInfo[v.algorithm.name].channelAlgorthm = v
              }
            })
          } else {
            console.log('')
          }
        },
        utils.defaultApiErrorHandler(endpoint, ``)
      )
    }
  },
  watch: {
    channelId () {
      this.loadChannelAlgorithms()
    },
    currAlgName () {

    }
  },
  mounted () {
    $('a[data-toggle="list"]').on('shown.bs.tab', (event) => {
      console.log('shown.bs.tab')
      console.log($(event.target), $(event.target).attr('data-x')) // newly activated tab
      console.log(event.relatedTarget) // previous active tab
      this.currAlgName = $(event.target).attr('data-x')
    })
  }
}
</script>

<style>
.icon-name{
  display: flex;
  height: 50px;
  align-items: center;
  justify-content: left;
}
</style>
